// Importa la libreria de React
import React from "react";
import ReactDOM from "react-dom/client"; // Importa ReactDOM para renderizar la aplicacin en el DOM
import App from "./App"; // Importa el componente App
import { BrowserRouter } from "react-router-dom"; // Importa BrowserRouter de react-router-dom

// Crea una raz para la aplicacin en el elemento con id "root"
const root = ReactDOM.createRoot(document.getElementById("root"));
// Renderiza la aplicacin
root.render(
  <BrowserRouter basename="/dealhunter">
    {/* Renderiza el componente App, el componente principal de la aplicacin */}
    <App />
  </BrowserRouter>
);
